.phone {
    font-weight: 700;
    font-size: 16px;
    line-height: 20px;
    color:$white;
    display: flex;
    align-items: center;
    margin: 0 -15px;
    &--about {
        color: $grey;
    }
    &--color {
        color: $number-color;
        padding: 10px 0;

    }
}

@media(min-width:482px)  {
    .phone {
        &::after {
            content:'';
            display: inline-block;
              background:$border-color;
              border-radius: 50%;
              background-position: center;
              width: 6px;
              height: 6px;
              margin: 0 10px;
        }
        &::before {
              content:'';
              display: inline-block;
              background:$border-color;
              border-radius: 50%;
              background-position: center;
              width: 6px;
              height: 6px;
              margin: 0 6px;
    
        }
        &--color {
            &::after {
                background:$grey;
            }
            &::before {
                background:$grey;
            }
        }
        
     }
    }

