.img-responsive {
    width: 100%;
    &--height {
      height: 100%;
    }
    &--text {
      font-weight: 700;
      font-size: 36px;
      line-height: 44px;
      text-align: center;
      letter-spacing: 13px;
      text-transform: uppercase;
      color: $white;
      position: absolute;
      top: 50%; 
      left: 50%; 
      transform: translate(-50%, -50%); 
      opacity: 0.8;
      text-shadow: 0px 15px 30px rgba(40, 40, 86, 0.8);

    }
}

@media(min-width:482px) { 
  .img-responsive {
    &--height-2 {
      height:200px ;
      object-fit: cover;
      border-radius: 8px 8px 0px 0px;
    }

    &--text {
      font-size: 46px;
      line-height: 56px;
      letter-spacing: 63px;
    }
  }
}


@media(min-width:868px) { 
  .img-responsive {
    &--text {
      font-size: 86px;
      line-height: 105px;
  }
}
}